:root{
    --light-gray: #2b3032a1; 
    --primary-color: #45ccb8; 
    --border: #2b303218; 
    --text-color: #ff686b; 
    --roboto: "Roboto",sans-serif; 
    --gugi: "Gugi",cursive; 
    --sofia:"Sofia",cursive;
}

.primary-color{
    color:#45ccb8;
    color:var(--primary-color);
}
.bg-primary-color{
    background-color:#45ccb8!important ;
    background-color:var(--primary-color)!important ;
}

.text-color{
    color:#ff686b;
    color:var(--text-color);
}
.front-roboto{
    font-family:"Roboto",sans-serif;
    font-family:var(--roboto);
}

.lightblue{
    color: lightblue;
}

/* SECTION header */

header{
